.hero {
  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: 0.03em;
  color: var();
}
.hero__description-gray {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.57;
  color: var();
}
.hero__description {
  font-size: 14px;
  line-height: 1.57;
  color: var();
}
.hero__description-orange {
  font-size: 14px;
  line-height: 1.57;
  color: var();
}
.hero__button {
  font-size: 12px;
  line-height: 1.25;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.08em;
  text-transform: uppercase;

  color: var();

  display: block;
  min-width: 260px;
  min-height: 48px;
  padding: 14px;

  background-color: var(--accent-color);
  //   padding: 10px 32px;
  margin: 0 auto;
  cursor: pointer;
}
